@import url("./Containers/Header/Header.module.css");
@import url("./Containers/Header/StickyHeader.module.css");
@import url("./Containers/Banner/Banner.module.css");
@import url("./Containers/Banner/Typedjs.module.css");
@import url("./Containers/Home/Home.module.css");
@import url("./Containers/Gallery/Gallery.module.css");
@import url("./Containers/Footer/Footer.module.css");
@import url("./Containers/Home/Button.module.css");
@import url("./Containers/Home/Home.module.css");
@import url("./Containers/Home/Polygons.module.css");
@import url("./Containers/Testimonials/Testimonials.module.css");
@import url("./Containers/Testimonials/Avatar.module.css");
@import url("./Containers/Header/MobileMenu/MobileMenu.module.css");

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  height: 100%;
  width: 100%;
  text-rendering: optimizeLegibility;
  letter-spacing: .06rem;
  line-height: 2rem;
  font-family: 'Open Sans', sans-serif;
}

a,
abbr,
area,
article,
aside,
audio,
b,
bdo,
blockquote,
body,
button,
canvas,
caption,
cite,
code,
col,
colgroup,
command,
datalist,
dd,
del,
details,
dfn,
dialog,
div,
dl,
dt,
em,
embed,
fieldset,
figure,
form,
h1,
h2,
h3,
h4,
h5,
h6,
head,
header,
hgroup,
hr,
html,
i,
iframe,
img,
input,
ins,
kbd,
keygen,
label,
legend,
li,
map,
mark,
menu,
meter,
nav,
noscript,
object,
ol,
optgroup,
option,
output,
p,
param,
pre,
progress,
q,
rp,
rt,
ruby,
samp,
section,
select,
small,
span,
strong,
sub,
sup,
table,
tbody,
td,
textarea,
tfoot,
th,
thead,
time,
tr,
ul,
var,
video {
  background: transparent;
  border: 0;
  font-size: 100%;
  margin: 0;
  outline: none;
  padding: 0;
  text-align: left;
  text-decoration: none;
  vertical-align: baseline;
}

h1 {
  color: #254A5D !important;
  font-size: 30px;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  margin-top: 5px;
}

h2 {
  color: #254A5D;
  font-size: 30px;
  text-decoration: none;
  line-height: 2.5;
}

h3 {
  color: #254A5D;
  font-size: 18px;
  text-decoration: none;
  line-height: 2.5;
}

p {
  margin: 0 0 20px;
}

a {
  color: #254A5D !important;
}

.container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.align-center {
  text-align: center;
}

.display-left {
  position: absolute;
  left: 0%;
  transform: translate(0%, -50%);
}

.display-right {
  position: absolute;
  right: 0%;
  transform: translate(-0%, -50%);
}

.white {
  color: #fff;
}

.main-container {
  max-width: 1200px;
  margin: 0 auto;
}

#first-child {
  padding-top: 100px;
}



a:hover {
  text-decoration: none !important;
}


@media (min-width: 575px) and (max-width: 768px) {
  .main-header-center {
    margin-left: 10px;
  }

  .ml4 {
    font-size: 3em;
    top: -120px;
  }

  .testimonials-slide {
    margin: 0;
  }

  .sticky-header-left {
    max-width: 150px;
  }
}

@media (min-width: 481px) and (max-width: 575px) {
  .main-header-top {
    height: 100px;
  }

  #first-child {
    padding-top: 50px;
  }

  .header {
    padding: 10px;
  }

  .ml4 {
    font-size: 3em;
    top: -120px;
  }

  #sticky-header {
    height: 80px;
  }

  .sticky-header-container {
    height: 80px;
  }

  .sticky-header-left {
    margin-top: 15px;
    padding-left: 10px;
  }

  #cta-wrapper-home {
    padding: 10px;
  }

  .section-wrapper {
    margin-top: 60px;
  }

  .section-slide {
    margin-top: 60px;
  }

  .section-testimonial {
    margin-top: 300px;
  }

  .gallery-content-nav {
    position: relative;
  }

  .prev,
  .next {
    margin-top: 0;
  }

  #divider {
    margin-bottom: 0;
  }
}

/*
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
  .main-header-top {
    height: 60px;
  }

  #first-child {
    padding-top: 50px;
  }

  .header {
    padding: 10px;
  }

  .ml4 {
    font-size: 3em;
    top: -100px;
  }

  .sticky-header-left {
    margin-top: 15px;
    padding-left: 10px;
  }

  #cta-wrapper-home {
    padding: 10px;
  }

  .section-wrapper {
    margin-top: 60px;
  }

  .section-slide {
    margin-top: 60px;
  }

  .gallery-content-nav {
    position: relative;
  }

  .prev,
  .next {
    margin-top: 0;
  }

  #divider {
    margin-bottom: 0;
  }

  .testimonials-slide {
    margin-left: 0;
  }

  .footer-header {
    padding-top: 20px;
  }

  .footer-contact {
    font-size: 20px;
  }

  .footer-header-inner {
    max-width: 480px;
    margin: 0 auto;
  }

  .footer-header-inner-left {
    width: 70%;
    float: right;
  }

  .footer-header-inner-right {
    width: 100%;
  }

  .social-links .icon {
    padding: 20px 0 5px 0;
    font-size: 15px;
  }

  .menu-item-has-children .menu-item-nav-footer .footer-bottom {
    font-size: 12px;
  }

  .menu-item-has-children {
    text-align: center;
  }

  .menu-item-nav-footer {
    text-align: center;
  }

  .footer-bottom {
    margin-top: 0;
    font-size: 12px;
  }
}

