  }

  .next {
    right: 0
  }

  #divider {
    margin-bottom: 0;
  }

  .testimonials-slide {
    margin-left: 0;
  }

  .footer-header {
    padding-top: 20px;
  }

  .footer-contact {
    font-size: 20px;
  }

  .footer-header-inner {
    max-width: 480px;
    margin: 0 auto;
  }

  .footer-header-inner-left {
    width: 70%;
    float: right;
  }

  .footer-header-inner-right {
    width: 100%;
  }

  .social-links .icon {
    padding: 20px 0 5px 0;
    font-size: 15px;
  }

  .menu-item-has-children .menu-item-nav-footer .footer-bottom {
    font-size: 12px;
  }

  .menu-item-has-children {
